<template>
    <div class="bigbox">
        <div class="newscardbox">
            <div class="toptitlebox" @click="send()">今日中国</div>
            <ul>
                <li v-for="(n,inx) in mynewslist.showapi_res_body.newslist" :key="inx">
                    <span><i class="el-icon-alarm-clock"></i>{{n.ctime}}</span>
                    <div>{{n.title}}</div>
                    <img :src="n.picUrl" alt="">
                    <a href=""></a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:'mynewscard',
    data() {
        return {
            mynewslist: {showapi_res_body:{newslist:[]}},
            nowtime:'',
        }
    },
    methods:{
    send:function(){
      var that = this;
      $.ajax({
        type: 'post',
        url: 'http://route.showapi.com/1824-1',
        dataType: 'json',
        data: {
            "showapi_timestamp": that.nowtime,
            "showapi_appid": '150928', //这里需要改成自己的appid
            "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需要改成自己的应用的密钥secret
            "col":"11",
            "num":"15",
            "page":"1"
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
            alert("操作失败!");
        },
        success: function(result) {
            that.mynewslist = result;
        }
      })
    },
    getTime:function(){
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth()+1<10 ? parseInt('0'+new Date().getMonth())+1 : new Date().getMonth()+1;
      let dd = new Date().getDate()<10 ? '0'+new Date().getDate() : new Date().getDate();
      let hh = new Date().getHours()<10 ? '0'+new Date().getHours() : new Date().getHours();
      let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
      let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
      this.nowtime = yy.toString()+mm+dd+hh+mf+ss;
    },
  },
  created(){
    this.getTime();
    this.send();
  }
}
</script>
<style scoped>
.bigbox{
    background-color: #F5F5F5;
    padding: 50px 110px 70px 110px;
}
.newscardbox{
    height: 830px;
    width: 100%;
}
.toptitlebox{
    height: 50px;
    width: 100%;
    line-height: 50px;
    font-size: 30px;
    font-weight: bold;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.newscardbox ul{
    width: 100%;
    
}
.newscardbox ul li{
    background-color: #f3f3f3;
    width: 20%;
    float: left;
    padding: 10px 10px 0 10px;
    cursor: pointer;
}
.newscardbox ul li span{
    display: block;
    height: 40px;
    width: 100%;
    line-height: 40px;
    background-color: #fff;
}
.newscardbox ul li div{
    height: 60px;
    width: 100%;
    line-height: 60px;
    background-color: #fff;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.newscardbox ul li img{
    background-color: #fff;
    width: 100%;
    height: 150px;
}
</style>


