<template>
    <div class="newsbox">
        <div class="titlebox">要闻</div>
        <div class="detailbox">
            <div class="inland">
                <div class="toptitle">
                    <div>国内新闻</div>
                    <span @click="inlandreflash"><i class="el-icon-refresh"></i>换一换</span>
                </div>
                <ul>
                    <li v-for="(n,inx) in mynewslist1.showapi_res_body.newslist" :key="inx"><a :href="n.url">{{n.title}}</a></li>
                </ul>
            </div>
            <div class="military">
                <div class="toptitle">
                    <div>军事新闻</div>
                    <span @click="militaryreflash"><i class="el-icon-refresh"></i>换一换</span>
                </div>
                <ul>
                    <li v-for="(n,inx) in mynewslist2.showapi_res_body.newslist" :key="inx"><a :href="n.url">{{n.title}}</a></li>
                </ul>
            </div>
            <div class="social">
                <div class="toptitle">
                    <div>社会新闻</div>
                    <span @click="socialreflash"><i class="el-icon-refresh"></i>换一换</span>
                </div>
                <ul>
                    <li v-for="(n,inx) in mynewslist3.showapi_res_body.newslist" :key="inx"><a :href="n.url">{{n.title}}</a></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'mynewsinland',
    data() {
        return {
            mynewslist1: {showapi_res_body:{newslist:[]}},
            mynewslist2: {showapi_res_body:{newslist:[]}},
            mynewslist3: {showapi_res_body:{newslist:[]}},
            nowtime:'',
            page1:1,
            page2:1,
            page3:1,
        }
    },
    methods:{
        inlandreflash:function(){
            this.page3++;
            this.send();
        },
        militaryreflash:function(){
            this.page1++;
            this.send1();
        },
        socialreflash:function(){
            this.page2++;
            this.send2();
        },
    send:function(){
      var that = this;
      $.ajax({
        type: 'post',
        url: 'http://route.showapi.com/1824-1',
        dataType: 'json',
        data: {
            "showapi_timestamp": that.nowtime,
            "showapi_appid": '150928', //这里需要改成自己的appid
            "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需要改成自己的应用的密钥secret
            "col":"7",
            "num":"6",
            "page":that.page3
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
            alert("操作失败!");
        },
        success: function(result) {
            that.mynewslist1 = result;
        }
      })
    },
    send2:function(){
      var that = this;
      $.ajax({
        type: 'post',
        url: 'http://route.showapi.com/1824-1',
        dataType: 'json',
        data: {
            "showapi_timestamp": that.nowtime,
            "showapi_appid": '150928', //这里需要改成自己的appid
            "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需要改成自己的应用的密钥secret
            "col":"5",
            "num":"6",
            "page":that.page2
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
            alert("操作失败!");
        },
        success: function(result) {
            that.mynewslist3 = result;
        }
      })
    },
    send1:function(){
      var that = this;
      $.ajax({
        type: 'post',
        url: 'http://route.showapi.com/1824-1',
        dataType: 'json',
        data: {
            "showapi_timestamp": that.nowtime,
            "showapi_appid": '150928', //这里需要改成自己的appid
            "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需要改成自己的应用的密钥secret
            "col":"27",
            "num":"6",
            "page":that.page1,
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
            alert("操作失败!");
        },
        success: function(result) {
            that.mynewslist2 = result;
        }
      })
    },
    getTime:function(){
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth()+1<10 ? parseInt('0'+new Date().getMonth())+1 : new Date().getMonth()+1;
      let dd = new Date().getDate()<10 ? '0'+new Date().getDate() : new Date().getDate();
      let hh = new Date().getHours()<10 ? '0'+new Date().getHours() : new Date().getHours();
      let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
      let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
      this.nowtime = yy.toString()+mm+dd+hh+mf+ss;
    },
  },
  created(){
    this.getTime();
    this.send();
    this.send1();
    this.send2();
  }
}
</script>

<style scoped>
a{
    text-decoration: none;
    color: #000;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.newsbox{
    width: 100%;
    height: 420px;
    padding: 20px 110px 0 110px;
    background-color: #F5F5F5;
}
.titlebox{
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 30px;
    font-weight: bold;
}
.detailbox{
    width: 100%;
    height: 350px;
    display: flex;
}
.inland,
.military,
.social{
    flex: 1;
    padding: 0 10px 0 10px;
    background-color: #fff;
    width: 100%;
}
.military,
.social{
    margin-left: 10px;
}
.toptitle{
    height: 50px;
    width: 100%;
    line-height: 50px;
    position: relative;
    color: #BF0915;
}
.toptitle div{
    height: 50px;
    width: 100%;
    font-size: 20px;
}
.toptitle span{
    display: block;
    height: 50px;
    position: absolute;
    right: 10px;
    top: 0;
    cursor: pointer;
    color: #C00715;
}
.inland ul,
.military ul,
.social ul{
    height: 300px;
    width: 100%;
}
.inland ul li,
.military ul li,
.social ul li{
    height: 50px;
    width: 100%;
    /* white-space: nowrap; */
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 50px;
    cursor: pointer;
}
.inland ul li a:hover,
.military ul li a:hover,
.social ul li a:hover{
    color: red;
}
</style>


