<template>
    <div>
        <div class="block" @click="send()">
          <el-carousel trigger="click" height="582px" direction="vertical" indicator-position="none" :interval=5000 @change="((pre) => {change(pre)})" ref="carouserl">
            <el-carousel-item v-for="(n,inx) in mynewslist.showapi_res_body.newslist" :key="inx" class="txt">
                <img :src="n.picUrl" alt="">
            </el-carousel-item>
          </el-carousel>
            <ul class="txtul">
                <li v-for="(n,inx) in mynewslist.showapi_res_body.newslist" :key="inx" @click="setactive(inx)">{{n.title}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
 import $ from 'jquery'

export default {
    name: 'myswipe',
    data() {
        return {
            mynewslist: {showapi_res_body:{newslist:[]}},
            nowtime:'',
        }
    },
    methods:{
        change(pre){
            var li = document.querySelector('.txtul').children;
            for(var i=0;i<li.length;i++){
                li[i].classList.remove("licurrent");
            }
            li[pre].classList.add("licurrent");
        },
        setactive:function(inx){
            this.setActiveItem(inx);
        },
        setActiveItem(inx){
            this.$refs.carouserl.setActiveItem(inx);
        },
        send:function(){
            var that = this;
            $.ajax({
              type: 'post',
              url: 'http://route.showapi.com/1824-1',
              dataType: 'json',
              data: {
                  "showapi_timestamp": that.nowtime,
                  "showapi_appid": '150928', //这里需要改成自己的appid
                  "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需 要 改成自己的应用的密钥secret
                  "col":"8",
                  "num":"10",
                  "page":"1"
              },
              error: function(XmlHttpRequest, textStatus, errorThrown) {
                  alert("操作失败!");
              },
              success: function(result) {
                  that.mynewslist = result;
              }
            })
    },
    getTime:function(){
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth()+1<10 ? parseInt('0'+new Date().getMonth())+1 : new Date().getMonth()+1;
      let dd = new Date().getDate()<10 ? '0'+new Date().getDate() : new Date().getDate();
      let hh = new Date().getHours()<10 ? '0'+new Date().getHours() : new Date().getHours();
      let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
      let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
      this.nowtime = yy.toString()+mm+dd+hh+mf+ss;
    },
  },
  created(){
    this.getTime();
    this.send();
  }
}
</script>

<style scoped>
ul,ol{
    list-style: none;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -khtml-user-select: none; /* Konqueror */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently not supported by any browser */
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
}
.el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
}
.block{
    position: relative;
}
.block ul{
    position: absolute;
    height: 100%;
    width: 320px;
    background-color: rgba(0, 0, 0,.7);
    right: 112px;
    top: 0;
    padding: 26px 0 26px 0;
    z-index: 999;
}
.txt img{
    height: 100%;
    width: 100%;
}
.block ul li{
    width: 100%;
    height: 53px;
    padding: 4px 10px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
}
.licurrent{
    padding: 0 0 0 2px!important;
    background-image: linear-gradient(to right, #BE0614 , #461B24);
    font-weight: bold;
    font-size: 20px!important;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
</style>


