<template>
    <div class="bigbox">
        <el-card>
            <el-table :data="mynewslist1.showapi_res_body.newslist" style="width: 100%" stripe >
                <el-table-column type="index" width="50"></el-table-column>
              <el-table-column prop="ctime" label="日期" width="180"></el-table-column>
              <el-table-column prop="title" label="标题" width="600"></el-table-column>
              <el-table-column prop="description" label="标签"></el-table-column>
              <el-table-column prop="url" label="地址">
                  <template slot-scope="scope">
                      <div>
                          <a :href="scope.row.url">查看</a>
                      </div>
                  </template>
              </el-table-column>
            </el-table>
            <el-pagination background layout="prev, pager, next" :total="100" style="margin-top:10px" @current-change="handleCurrentChange">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    name: 'myswipethree',
    data() {
        return {
            nowtime: '',
            mynewslist1: {showapi_res_body:{newslist:[]}},
        }
    },
    methods:{
        handleCurrentChange(val) {
            this.send(val);
      },
    send:function(page){
      var that = this;
      $.ajax({
        type: 'post',
        url: 'http://route.showapi.com/1824-1',
        dataType: 'json',
        data: {
            "showapi_timestamp": that.nowtime,
            "showapi_appid": '150928', //这里需要改成自己的appid
            "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需要改成自己的应用的密钥secret
            "col":"18",
            "num":"15",
            "page":page,
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
            alert("操作失败!");
        },
        success: function(result) {
            that.mynewslist1 = result;
        }
      })
    },
    getTime:function(){
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth()+1<10 ? parseInt('0'+new Date().getMonth())+1 : new Date().getMonth()+1;
      let dd = new Date().getDate()<10 ? '0'+new Date().getDate() : new Date().getDate();
      let hh = new Date().getHours()<10 ? '0'+new Date().getHours() : new Date().getHours();
      let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
      let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
      this.nowtime = yy.toString()+mm+dd+hh+mf+ss;
    },
  },
  created(){
    this.getTime();
    this.send(1);
  }
}
</script>

<style scoped>
.bigbox{
    width: 100%;
    height: 971px;
    background-color: #f5f5f5;
    padding: 60px;
}
</style>


