<template>
    <div class="embigbox">
        <div class="leftbox">
            <el-tabs type="border-card">
              <el-tab-pane label="影视">
                  <ul class="moveul">
                      <li v-for="(n,inx) in list[0].mynewslist.showapi_res_body.newslist" :key="inx">
                          <img :src="n.picUrl" alt="" onerror="this.src='http://bpic.588ku.com/element_origin_min_pic/01/37/85/11573c64bf22f10.jpg'">
                          <div>{{n.title}}</div>
                          <span>
                              <span>{{n.ctime}}</span>
                              <i class="glyphicon glyphicon-share"></i>
                          </span>
                          <a :href="n.url"></a>
                      </li>
                  </ul>
              </el-tab-pane>
              <el-tab-pane label="动漫">
                  <ul class="moveul">
                      <li v-for="(n,inx) in list[1].mynewslist.showapi_res_body.newslist" :key="inx">
                          <img :src="n.picUrl" alt="" onerror="this.src='http://bpic.588ku.com/element_origin_min_pic/01/37/85/11573c64bf22f10.jpg'">
                          <div>{{n.title}}</div>
                          <span>
                              <span>{{n.ctime}}</span>
                              <i class="glyphicon glyphicon-share"></i>
                          </span>
                          <a :href="n.url"></a>
                      </li>
                  </ul>
              </el-tab-pane>
              <el-tab-pane label="游戏">
                  <ul class="moveul">
                      <li v-for="(n,inx) in list[2].mynewslist.showapi_res_body.newslist" :key="inx">
                          <img :src="n.picUrl" alt="" onerror="this.src='http://bpic.588ku.com/element_origin_min_pic/01/37/85/11573c64bf22f10.jpg'">
                          <div>{{n.title}}</div>
                          <span>
                              <span>{{n.ctime}}</span>
                              <i class="glyphicon glyphicon-share"></i>
                          </span>
                          <a :href="n.url"></a>
                      </li>
                  </ul>
              </el-tab-pane>
              <el-tab-pane label="奇闻">
                  <ul class="moveul">
                      <li v-for="(n,inx) in list[3].mynewslist.showapi_res_body.newslist" :key="inx">
                          <img :src="n.picUrl" alt="" onerror="this.src='http://bpic.588ku.com/element_origin_min_pic/01/37/85/11573c64bf22f10.jpg'">
                          <div>{{n.title}}</div>
                          <span>
                              <span>{{n.ctime}}</span>
                              <i class="glyphicon glyphicon-share"></i>
                          </span>
                          <a :href="n.url"></a>
                      </li>
                  </ul>
              </el-tab-pane>
            </el-tabs>
        </div>
        <div class="rightbox">
            <div class="titlebox" @click="callmove(12,4,32)" style="cursor: pointer;">体育新闻</div>
            <ul>
                <li v-for="(n,inx) in list[4].mynewslist.showapi_res_body.newslist" :key="inx">
                    <img :src="n.picUrl" alt="">
                    <div>{{n.title}}</div>
                    <span>{{n.ctime}}</span>
                    <a :href="n.url"></a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: 'emleft',
    data() {
        return {
            list: [{
                mynewslist: {showapi_res_body:{newslist:[]}},
                inx: 0,
            },
            {
                mynewslist: {showapi_res_body:{newslist:[]}},
                inx: 1,
            },
            {
                mynewslist: {showapi_res_body:{newslist:[]}},
                inx: 2,
            },
            {
                mynewslist: {showapi_res_body:{newslist:[]}},
                inx: 3,
            },
            {
                mynewslist: {showapi_res_body:{newslist:[]}},
                inx: 4,
            },],
            nowtime:'',
        }
    },
    methods:{
        callmove(id,inx,num){
            this.send(id,inx,num);
        },
        send:function(id,inx,num){
            var that = this;
            $.ajax({
              type: 'post',
              url: 'http://route.showapi.com/1824-1',
              dataType: 'json',
              data: {
                  "showapi_timestamp": that.nowtime,
                  "showapi_appid": '150928', //这里需要改成自己的appid
                  "showapi_sign": '32e8661789ac4368b2ca4de91f08fc58',  //这里需 要 改成自己的应用的密钥secret
                  "col":id,
                  "num":num,
                  "page":"1"
              },
              error: function(XmlHttpRequest, textStatus, errorThrown) {
                  alert("操作失败!");
              },
              success: function(result) {
                  that.list[inx].mynewslist = result;
              }
            })
        },
        getTime:function(){
          let yy = new Date().getFullYear();
          let mm = new Date().getMonth()+1<10 ? parseInt('0'+new Date().getMonth())+1 : new Date().getMonth()+1;
          let dd = new Date().getDate()<10 ? '0'+new Date().getDate() : new Date().getDate();
          let hh = new Date().getHours()<10 ? '0'+new Date().getHours() : new Date().getHours();
          let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
          let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
          this.nowtime = yy.toString()+mm+dd+hh+mf+ss;
        },
    },
    created(){
        this.callmove(40,0,20);
        this.callmove(33,1,20);
        this.callmove(31,2,20);
        this.callmove(14,3,20);
        this.callmove(12,4,32);
    }
}
</script>

<style scoped>
.embigbox{
    background-color: #F5F5F5;
    height: 3321px;
}
.leftbox{
    width: 800px;
    background-color: #fff;
    float: left;
}
.rightbox{
    width: 380px;
    height: 3261px;
    background-color: #fff;
    float: left;
    margin-left: 20px;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.moveul{
    width: 100%;
}
.moveul li:nth-child(1){
    margin-top: 0;
}
.moveul li{
    width: 100%;
    height: 150px;
    margin-top: 10px;
    position: relative;
    border-bottom: 1px solid #ccc;
}
.moveul li img{
    display: block;
    height: 99%;
    width: 250px;
    float: left;
}
.moveul li div{
    height: 40px;
    padding: 0 10px 0 10px;
    width: 518px;
    float: left;
    font-size: 18px;
    line-height: 40px;
    overflow: hidden;/*超出部分隐藏*/
    white-space: nowrap;/*不换行*/
    text-overflow:ellipsis;/*超出部分文字以...显示*/
}
.moveul li span{
    display: block;
    height: 30px;
    width: 518px;
    position: absolute;
    bottom: 0;
    right: 0;
}
.moveul li span span{
    position: absolute;
    display: block;
    height: 30px;
    width: 200px;
    left: 10px;
    line-height: 30px;
}
.moveul li span i{
    position: absolute;
    display: block;
    height: 30px;
    width: 30px;
    right: 10px;
    cursor: pointer;
}
.moveul li a{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.titlebox{
    height: 40px;
    font-size: 20px;
    padding-left: 10px;
    line-height: 40px;
}
.rightbox ul{
    width: 100%;
}
.rightbox ul li{
    width: 100%;
    height: 100px;
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #ccc;
    position: relative;
}
.rightbox ul li img{
    display: block;
    height: 99%;
    width: 100px;
    float: left;
}
.rightbox ul li div{
    height: 60px;
    width: 250px;
    float: left;
    font-style: 20px;
    padding-left: 5px;
}
.rightbox ul li span{
    display: block;
    height: 19px;
    width: 250px;
    float: left;
    padding-left: 5px;
}
.rightbox ul li a{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
</style>


