import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import myheader from '@/components/myheader'
import myswipe from '@/components/myswipe'
import mynewsinland from '@/components/mynewsinland'
import mybottom from '@/components/mybottom'
import mynewscard from '@/components/mynewscard'
import myswipetwo from '@/components/myswipetwo'
import emleft from '@/components/emleft'
import myhealth from '@/components/myhealth'
import myswipethree from '@/components/myswipethree'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/myheader',
      name: 'myheader',
      component: myheader
    },
    {
      path: '/myswipe',
      name: 'myswipe',
      component: myswipe
    },
    {
      path: '/mynewsinland',
      name: 'mynewsinland',
      component: mynewsinland
    },
    {
      path: '/mybottom',
      name: 'mybottom',
      component: mybottom
    },
    {
      path: '/mynewscard',
      name: 'mynewscard',
      component: mynewscard
    },{
      path: '/myswipetwo',
      name: 'myswipetwo',
      component: myswipetwo
    },
    {
      path: '/emleft',
      name: 'emleft',
      component: emleft
    },
    {
      path: '/myhealth',
      name: 'myhealth',
      component: myhealth
    },
    {
      path: '/myswipethree',
      name: 'myswipethree',
      component: myswipethree
    },
  ]
})



