// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'jquery'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap'
import {Icon,Carousel,CarouselItem,Tabs,TabPane,Card,Table,TableColumn,Pagination } from 'element-ui'

Vue.use(Icon).use(Carousel).use(CarouselItem).use(Tabs).use(TabPane).use(Card).use(Table).use(TableColumn)
   .use(Pagination)

Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



