<template>
    <div>
        <header>
            <div class="logobox not-select"><span class="glyphicon glyphicon-hand-right"></span>asui.club</div>
            <div class="headerfirstbtn" @click="changepage(1)">时事<span v-if="pageflag==1" class="glyphicon glyphicon-menu-down"></span></div>
            <div class="headersecondbtn" @click="changepage(2)">娱乐<span v-if="pageflag==2" class="glyphicon glyphicon-menu-down"></span></div>
            <div class="headerthirdbtn" @click="changepage(3)">健康<span v-if="pageflag==3" class="glyphicon glyphicon-menu-down"></span></div>
            <div class="headersearchbox">
                <input type="search" placeholder="搜索新闻..." id="headersearch" v-model="headerSearchSalue">
                <i class="el-icon-close" @click="headerclear"></i>
                <span class="el-icon-search"></span>
                <img src="../assets/vx.jpg" alt="" title="我的微信公众号">
            </div>
        </header>
    </div>
</template>

<script>
export default {
    name:'myheader',
    data() {
        return {
            headerSearchSalue: '',
            pageflag: 1,
        }
    },
    methods:{
        headerclear:function(){
            this.headerSearchSalue = '';
        },
        changepage(pageid){
            this.pageflag = pageid;
            this.$parent.changePage(pageid);
        }
    
    },
}
</script>

<style>
@media only screen and (max-width: 1199px) {
    header{
        padding: 0 105px 0 105px;
    }
    .headersearchbox img{
        display: none;
    }
}
@media only screen and (max-width: 1572px) {
    .embigbox{
        padding: 60px 0 0 0;
    }
}
@media only screen and (min-width: 1573px) and (max-width: 1700px) {
    .embigbox{
        padding: 60px 132px 0 132px;
    }
}
@media only screen and (min-width: 1200px) and (max-width: 1700px){
    header{
        padding: 0 160px 0 160px;
    }
}
</style>
<style scoped>
header{
    height: 60px;
    width: 100%;
    min-width: 1200px;
    background-color: #333333;
    /* padding: 0 160px 0 160px; */
    color: #fff;
}
.logobox{
    height: 76px;
    width: 160px;
    background-color: #BF0614;
    line-height: 76px;
    font-size: 30px;
    cursor: pointer;
    box-shadow: 2px 2px 2px rgb(77, 77, 77);
    float: left;
}
.logobox span{
    font-size: 18px;
    color: #ccc;
    margin-right: 8px;
}
.not-select{
    -moz-user-select:none; /*火狐*/
    -webkit-user-select:none; /*webkit浏览器*/
    -ms-user-select:none; /*IE10*/
    -khtml-user-select:none; /*早期浏览器*/
    user-select:none;
}
a{
    text-decoration: none;
    color: #fff;
}
.headerfirstbtn,
.headersecondbtn,
.headerthirdbtn{
    float: left;
    height: 60px;
    line-height: 60px;
    width: 100px;
    text-align: center;
    margin-left: 80px;
    cursor: pointer;
    font-size: 16px;
}
.headerfirstbtn:hover,
.headersecondbtn:hover,
.headerthirdbtn:hover{
    color: red;
}
.headerfirstbtn:hover span,
.headersecondbtn:hover span,
.headerthirdbtn:hover span{
    color: red;
}
.headerfirstbtn span,
.headersecondbtn span,
.headerthirdbtn span{
    color: #ccc;
    margin-left: 10px;
}
.headersearchbox{
    float: left;
    height: 60px;
    margin-left: 50px;
    position: relative;
}
.headersearchbox input{
    outline: none;
    border: none;
    color: #000;
    height: 25px;
    position: absolute;
    top: 50%;
    width: 250px;
    transform: translateY(-50%);
    border-radius: 5px;
    padding-left: 22px;
}
.headersearchbox i{
    height: 15px;
    width: 15px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 99;
    left: 230px;
    color: #000;
    cursor: pointer;
    border-radius: 50%;
}
.headersearchbox i:hover{
    background-color: rgb(231, 231, 231);
}
.headersearchbox img{
    position: absolute;
    height: 60px;
    width: 60px;
    top: 0;
    left: 400px;
    cursor: pointer;
}
.headersearchbox span{
    display: block;
    position: absolute;
    height: 15px;
    width: 15px;
    color: #000;
    top: 50%;
    transform: translateY(-50%);
    left: 5px;
    cursor: pointer;
}
</style>


