<template>
  <div>
  <!-- 页面1 -->
  <div v-if="pageflag==1">
    <myheader></myheader>
    <div class="container">
      <div class="titlebox">
        <h1>头条 · <span>这份简历带你从寒冬走向暖春</span></h1>
      </div>
      <div class="swipebox">
        <myswipe></myswipe>
      </div>
      <div class="newsinland">
        <mynewsinland></mynewsinland>
      </div>
      <div class="newscardbox">
        <mynewscard></mynewscard>
      </div>
    </div>
    <div class="bottombox">
        <mybottom></mybottom>
      </div>
  </div>
  <!-- 页面2 -->
  <div v-if="pageflag==2">
    <myheader></myheader>
    <div class="container">
      <div class="swipebox" style="margin-top:30px">
        <myswipetwo></myswipetwo>
      </div>
      <div class="leftbox">
        <emleft></emleft>
      </div>
    </div>
    <div class="bottombox">
        <mybottom></mybottom>
    </div>
  </div>
  <!-- 页面3 -->
  <div v-if="pageflag==3">
    <myheader></myheader>
    <div class="container">
      <div class="swipebox" style="margin-top:30px">
        <myswipethree></myswipethree>
      </div>
      <div class="healthbox">
        <myhealth></myhealth>
      </div>
    </div>
    <div class="bottombox">
        <mybottom></mybottom>
    </div>
  </div>
  </div>
</template>

<script>
 import $ from 'jquery'
 import myheader from './myheader'
 import myswipe from './myswipe'
 import mynewsinland from './mynewsinland'
 import mybottom from './mybottom'
 import mynewscard from './mynewscard'
 import myswipetwo from './myswipetwo'
 import emleft from './emleft'
 import myhealth from './myhealth'
 import myswipethree from './myswipethree'

export default {
  name: 'index',
  components:{myheader,myswipe,mynewsinland,mybottom,mynewscard,myswipetwo,emleft,myhealth,myswipethree},
  data () {
    return {
      pageflag: 1,
    }
  },
  methods:{
    changePage(pageid){
      this.pageflag = pageid;
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'background-color:#f5f5f5')
  },
  beforeDestroy () {
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
  width: 94%;
  min-width: 1200px;
  padding-right: 0;
  padding-left: 0;
  background-color: #fff;
}
.titlebox{
  width: 100%;
}
.titlebox h1{
  height: 50px;
  width: 100%;
  font-weight: bold;
  color: #BF0614;
  line-height: 50px;
  text-align: center;
}
.titlebox h1 span:hover{
  color: #BF0614;
}
.titlebox h1 span{
  color: #000;
  cursor: pointer;
}
.swipebox{
  width: 100%;
}
.newsinland,
.bottombox,
.newscardbox,
.healthbox{
  width: 100%;
}
.leftbox{
  width: 100%;
}
</style>



